/*------AVATAR-------*/
.avatarImg img{
    width: 20%;
    display: block;
    margin-left: auto;
    margin-right: auto;
    border-radius: 100%;
}
/*-----Dialog Item-----*/

.dialogsItem{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: left;
    width: 95%;
    background-color: #edf2f6;
    border-radius: 5px;
    margin: 7px;
}

.dialogsItem .imgContainer{
    width: 50px;
    height: 50px;
    position: relative;
    overflow: hidden;
    border-radius: 35px;
    margin: 5px;
}

.dialogsItem img{
    width: 100%;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    height: 100%;

}

.dialogsItem a{
    display: block;
    text-decoration: none;
    color: #34465d;
    padding: 8px 7px;
    font-size: 14px;
}
/*------Message--------*/
.messageBox{
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    align-items: center;
    justify-content: space-between;
    margin: 4px 15px;
    /*position: relative;*/
    border-radius: 15px;
    /*border: 3px solid #7750F8;*/
    background-color: #edf2f6;
}

.messageBox img{
    width: 40px;
    height: 40px;
    position: relative;
    border-radius: 100%;
    border: 2px solid white;

}

.messageText{
    margin: 0 10px;
    background-color: #edf2f6;
    display: flex;
    flex-direction: row;
    color: #515365;
    font-size: 15px;
    padding: 5px;
    border-radius: 10px;
}
.messageTime{
    font-size: 12px;
    color: #555560;
    padding: 5px;
    border-radius: 10px;
}

/*------Dialogs------*/
.dialogs{
    min-width: 600px;
    /*max-width: 900px;*/
    height: 100%;
    background-color: #EDF2F6;
    /*margin: 65px 0 30px;*/
    border-radius: 5px;
    box-shadow: 3px 3px 7px #b9b9b9;
    /*border: 3px solid green;*/
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: flex-start;
    /*position: absolute;*/
}

.dialogs .dialogsBox{
    background-color: #ffffff;
    border-radius: 8px;
    font-size: 14px;
    height: 100%;
    display: flex;
    flex-direction: column;
    width: 35%;
    border: 1px solid #edf2f6;
}

.dialogs .messagesBox{
    /*width: 65%;*/
    height: 100%;
    background-color: #ffffff;
    display: flex;
    flex-wrap: nowrap;
    flex-direction: column;
    align-items: flex-end;
    justify-content: center;
    border: 1px solid #edf2f6;
    border-radius: 8px;
}
/*-----Messages-----*/
.inputMessage{
    margin: 15px;
    font-size: 14px;
    width: 90%;
    border-radius: 10px;
    font-family: "Roboto", "Segoe Ui";

}
.messagesBox .sendBtn{
    margin: 0 15px;
    background-color: white;
    color: #f5604f;
    padding: 8px 12px;
    border-radius: 12px;
    font-weight: 700;
    font-size: 15px;
    cursor: pointer;
    left: 60px;
    top: 20px;
    border: 1px solid #f5604f;
}
.sendBtn:hover{
    background-color: #f5604f;
    color: white;
    border: 1px solid #f5604f;
    padding: 8px 10px;
    border-radius: 12px;

}





