.navBar{
    position: fixed;
    top: 0px;
    left: 0px;
}
.iconContainer {
    display:flex;
    flex-direction: column;
    flex-wrap: wrap;
    justify-content: flex-start;
    align-items: center;
    background-color: #ffffff;
    width: 70px;
    height: 100vh;
    position: absolute;
    left: 0;
    padding-top: 80px;
}
.icon i{
    width: 24px;
    height: 24px;
    margin: 20px 0;
    padding: 10px;
    border-radius: 100%;
    background-color: #edf2f6;
    color: #515365;
    position: relative;
}

a{
    color: #535165;
    display: inline-block;
    font-size: 18px;
    position: relative;
    text-decoration: none;
    text-align: center;    
}
.icon i:hover{
    background-color: #f5604f;
    box-shadow: 5px 5px 15px rgba(0, 0, 0, 0.1);
    color: white;
}
.friendsContainer{
    display:flex;
    flex-direction: column;
    flex-wrap: wrap;
    align-content: stretch;
    justify-content: flex-start;
    align-items: center;
    background-color: #ffffff;
    width: 70px;
    height: 100vh;
    position: fixed;
    right: 0;
}
.friendsSideBar{
    position: relative;
    padding: 0;
    margin: 0;
}
.friendSideBarItem a{
    width: 24px;
    height: 24px;
    /*align-items: center;*/
    padding: 10px;
    border-radius: 100%;
    background-color: #edf2f6;
    margin: 15px;
    color: #515365;
    top: 10px;
    position: relative;
}


