.photosBox {
    width: 55%;
    background-color: #EDF2F6;
    margin: 65px auto 30px;
    border-radius: 5px;
    height: 100vh;
    display: flex;
    flex-direction: column;
}
.photosHeader{
    background-color: white;
    color: #515365;
    display: flex;
    flex-direction: row;
    border-left: 3px solid #f5604f;
    padding:  0 20px;
    font-size: 18px;
    border-radius: 5px;
    text-align: left;
    align-items: center;
    justify-content: space-between;
}
.photosHeader span{
    padding: 5px;
    border-radius: 100%;
    color: white;
    font-size: 11px;
    background-color: #7750f8;
}
.photos{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
    background-color: white;
    border-radius: 5px;
    margin-top: 15px;
    padding: 10px 0;
}
.photoCard img{
    margin: 10px;
    overflow: hidden;
    -moz-transition: all 2s ease-out;
    -o-transition: all 2s ease-out;
    -webkit-transition: all 2s ease-out;
}
.photoCard:hover img{
    margin: 10px;
    transform: scale(1.1 );
    -webkit-transform: scale(1.1);
    -moz-transform: scale(1.1);
    -o-transform: scale(1.1);
    border-radius: 8px;
    overflow: hidden;
    transition: all 0.2s linear 0s;
}
.photoCard{
    margin: 10px;
    overflow: hidden;
    position: relative;
}
.photoImg {
    width: 210px;
    position: relative;
    border-radius: 8px;
    overflow: hidden;
    transition: transform 0.25s;
    -o-transition: all 1s ease-out;
    -webkit-transition: all 1s ease-out;

}
.photoTextOver{
    color: #7750F8;
    position: absolute;
    /*background: linear-gradient(to bottom, rgba(0,0,0,0) 0%,rgba(0,0,0,0.65) 83%);*/
    align-items: center;
    border-radius: 0 0 6px 6px;
    bottom: 0;
    opacity: 0;
    transform:translate(-50%, 0);
    transition: 0.5s;
    width:98%;
    font-size: 14px;
    background-color:rgba(0, 0, 0, 0.5);
}
.photoTextOver:hover i{
    color: #7750F8;
    opacity: 1;
}
.photoTextOver i:hover{
    color: #f5604f;
}
.photoTextOver:hover .text{
    color: #7750F8;
    font-size: 14px;
}
