.search {
    position: relative;
    margin-right: 20px;
}

.searchInput {
    width: 250px;
    padding: 13px 10px 13px 20px;
    border: 1px solid #b9b9b9;
    background-color: rgba(255, 255, 255, 0.1);
    font-size: calc(11px + (12 - 11) * ((100vw - 320px) / (1920 - 320)));
    border-radius: 5px;
}

.wrapBtn {
    position: absolute;
    top: 55%;
    right: 10px;
    transform: translateY(-50%);
}

.btnSearch {
    position: relative;
}
input {
    width: 20px;
    height: 17px;
    border: none;
    /*color: #9a9fbf;*/
    background: transparent;
}
svg {
    position: absolute;
    top: 0;
    left: 0;
    width: 20px;
    height: auto;
    fill: #9a9fbf;
}




