.friendsPage {
    margin: 0 auto;
    padding: 65px 25px 25px;
    /*max-width: 400px;*/
    min-width: 400px;
    background-color: #ffffff;
    text-align: center;
}

.friendCard{
    width:200px;
    height: 350px;
    color: #757a95;
    font-size: 14px;
    background-color: #ffffff;
    /*font-family: "Roboto", "Segoe Ui";*/
    border-radius: 5px;
    position: relative;
    box-shadow: 0 2px 5px #ccc;
    text-align: left;
    display: inline-block;
    /*padding: 15px 0;*/
    margin: 5px 15px;

}
.friendCard figure{
    margin: 0;
    position: relative;
}

.cardTop {
    border-radius: 15px 15px 0 0;
    height: 100px;
    width: 100%;
    background-color: #ffffff;
    position: absolute;
    top: 0;
    left: 0;
    display: flex;
    flex-direction: column;
}
.cardCapture{
    position: absolute;
    width: 100%;
    text-align: right;
    right: 5px;
}
.friendName {
    font-size: 15px;
    font-weight: 500;
    color: #313345;
    display: inline-block;
    position: relative;
    width: 100%;
    text-align: right;
}
.friendName a{
    text-decoration: none;
    background-color: transparent;
}
.friendName:hover{
    color: #f5604f;
}
.cardCapture span{
    display: inline-block;
    font-size: 12px;
    color: #757a95;
    position: relative;
    text-align: right;
}
.bgImg{
    width: 100%;
    height: 100%;
    left: 0;
    position: absolute;
    right: 0;
    top: 0;
    border-radius: 4px;
}

.followersSpan{
    z-index: 2;
    position: relative;
    color: #ffffff;
    font-weight: 600;
    top: 80px;
    right: 0;
    left: 48%;
}
.friendAvatar img {
    object-fit: cover;
    border-radius: 100%;
    display: inline-block;
    margin-top: -40px;
    position: absolute;
    vertical-align: middle;
    z-index: 555;

}

.friendAvatar {
    border-radius: 50%;
    width: 80px;
    height: 80px;
    border: 4px solid #fff;
    position: relative;
    top: 40px;
    overflow: hidden;
}

.bottom {
    margin-top: 120px;
}

.friendInfo {
    margin: 20px 0;
    color: #515365;
    display: inline-block;
    padding: 0 15px 20px;
    position: relative;
    width: 100%;
    top: 8%;
    line-height: 1.6em;
}
.friendInfo li span{
    font-weight: 500;
}
.friendInfo li {
    color: #757a95;
}

.sendMessage a{
    text-decoration: none;
    background-color: transparent;
}
.sendMessage{
    background-color: white;
    color: #f5604f;
    padding: 8px 12px;
    border-radius: 12px;
    font-weight: 700;
    font-size: 15px;
    cursor: pointer;
    left: 60px;
    position: relative;
    top: 20px;
    border: 1px solid #f5604f;
}
.sendMessage:hover{
    background-color: #f5604f;
    color: white;
    border: 1px solid #f5604f;
    padding: 8px 10px;
    border-radius: 12px;

}
