header{
    background-color: #34465d;
    width: 100%;
    height: 65px;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 50;
}
.header__logo img {
    width: 30px;
    height: 30px;
}
.header__logo{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    padding: 15px;
    flex-wrap: nowrap;
    width: 90px;
    height: 30px;
}
.header__container{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-evenly;
}
.logoName{
    color: white;
    padding: 0 5px;
}
.header__nav{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-evenly;
}
.input_header{
    background: rgba(255,255,255,.1);
    border: medium none;
    font-size: 13px;
    padding: 13px 30px;
    width: 100%;
    color: #fff;
    -webkit-border-radius: 30px;
    -moz-border-radius: 30px;
    border-radius: 30px;
}
.page_name{
    border-right: 1px solid rgba(255, 255, 255, 0.1);
    color: #fff;
    float: left;
    font-size: 14px;
    font-weight: 500;
    line-height: 65px;
    padding-left: 30px;
    padding-right: 30px;
    position: relative;
    text-align: left;
    text-transform: uppercase;
    font-family: 'Roboto', sans-serif;
}
.header__nav_icons{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    float: left;
}
.nav_item{
    width: 50px;
    position: relative;
}
.nav_item .badge {
    position: absolute;
    top: 0px;
    right: 3px;
    padding: 1px 4px;
    border-radius: 100%;
    color: white;
    font-size: 11px;
    background-color: #ff5e3a;
}
.nav_item .badge2 {
    position: absolute;
    top: 0px;
    right: 3px;
    padding: 1px 4px;
    border-radius: 100%;
    color: white;
    font-size: 11px;
    background-color: #7750f8;
}
.nav_item .badge3 {
    position: absolute;
    top: 0px;
    right: 3px;
    padding: 1px 4px;
    border-radius: 100%;
    color: white;
    font-size: 11px;
    background-color: #23d2e2;
}

.nav_item i{
    width: 18px;
    height: 18px;
    padding: 10px;
    color: #b9b9b9;
}
.nav_item i:hover {
    align-items: center;
    justify-content: center;
    border-radius: 100%;
    border: 1px solid #343a40;
    padding: 10px;
    /*transition: all 0.2s;*/
    background-color: rgba(57, 58, 65, 0.7);
    color: white;
}
