.myAvatarImg{
    position: relative;
    top: 25px;
    padding-left: 5%;
    z-index: 25;
    text-align: left;
}
.myAvatarImg img{
    border: 3px solid #fff;
    border-radius: 100%;
    max-width: 155px;
    min-width: 155px;
    width: 155px;
    display: inline-block;
    
}
.profileBox{
    background: url("https://img.rawpixel.com/s3fs-private/rawpixel_images/website_content/v1013-p-0024c-01-ksi8b56b.jpg?w=1200&h=1200&dpr=1&fit=clip&crop=default&fm=jpg&q=75&vib=3&con=3&usm=15&cs=srgb&bg=F4F4F3&ixlib=js-2.2.1&s=5d6b5a766f8fa17e313c68144690c315") no-repeat center;
    width: 100%;
    height: 230px;
    border-radius: 5px;
    margin-bottom: 30px;
    min-width: 470px;
}
.myName{
    position: relative;
    display: flex;
    flex-direction: column;
    background-color: #fff;
    height: 55px;
    align-items: center;
    padding-left: 5%;
}
.myNameHeader{
    color: #515365;
}
.myNameHeader:hover{
    color: #ff5e3a;
    cursor: pointer;
}
.myLocation{
    color: #9a9fbf;
    font-size: 12px;

    margin-top: -20px;
}
.profileNav {
    background-color: #fff;
    width: 100%;
    border-bottom: 2px solid #ff5e3a;
    position: relative;
    display: flex;
    flex-direction: row;
    height: 65px;
    /* top: 20px; */
    justify-content: space-between;
    align-content: center;
    flex-wrap: nowrap;
    align-items: center;

}
.navList{
    display: flex;
    flex-direction: row;
    font-family: 'Roboto', sans-serif;
    position: relative;
}

.navItem {
    font-family: 'Roboto', sans-serif;
    font-weight: 500;
    position: relative;
    font-size: 15px;
    color: #515365;
    padding: 10px 20px;
    
}
.navItem:hover{
    color:#ff5e3a;
    text-underline-position: below;
    cursor: pointer;
}
