.videosBox {
    width: 55%;
    background-color: #EDF2F6;
    margin: 65px auto 30px;
    border-radius: 5px;
    height: 100vh;
    display: flex;
    flex-direction: column;
}
.videosHeader{
    background-color: white;
    color: #515365;
    display: flex;
    flex-direction: row;
    border-left: 3px solid #f5604f;
    padding:  0 20px;
    font-size: 18px;
    border-radius: 5px;
    text-align: left;
    align-items: center;
    justify-content: space-between;
}
.videosHeader span{
    padding: 5px;
    border-radius: 100%;
    color: white;
    font-size: 11px;
    background-color: #7750f8;
}
.videos{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
    background-color: white;
    border-radius: 5px;
    margin-top: 15px;
    padding: 10px 0;
}
.videoCard a{
    text-decoration: none;
    -moz-transition: all 2s ease-out;
    -o-transition: all 2s ease-out;
    -webkit-transition: all 2s ease-out;
}
.videoCard a:hover{
    -webkit-transform: scale(1.1);
    -moz-transform: scale(1.1);
    -o-transform: scale(1.1);
}
.videoCard img {
    width: 210px;
    position: relative;
    border-radius: 5px;
    overflow: hidden;
    transition: transform 0.25s;
    -o-transition: all 1s ease-out;
    -webkit-transition: all 1s ease-out;
}

.videoCard{
    /*padding: 10px 5px;*/
    margin: 10px;
    overflow: hidden;
}
.videoCard i{
    position: absolute;
    color: rgba(255, 255, 255, 0.8);
    background-color: rgba(245, 96, 79, 0.61);
    border-radius: 100%;
    top: 24%;
    left: 38%;
    width: 30px;
    height: 30px;
    border: 1px solid rgba(255, 255, 255, 0.75);
    font-size: 30px;
    padding: 10px;
}
.search {
    position: relative;
    margin-right: 20px;
}

.searchInput {
    width: 250px;
    padding: 13px 10px 13px 20px;
    border: 1px solid #b9b9b9;
    background-color: rgba(255, 255, 255, 0.1);
    font-size: calc(11px + (12 - 11) * ((100vw - 320px) / (1920 - 320)));
    border-radius: 5px;
}


.wrapBtn {
    position: absolute;
    top: 55%;
    right: 10px;
    transform: translateY(-50%);
}

.btnSearch {
    position: relative;
}
input {
    width: 20px;
    height: 17px;
    border: none;
    /*color: #9a9fbf;*/
    background: transparent;
}
svg {
    position: absolute;
    top: 0;
    left: 0;
    width: 20px;
    height: auto;
    fill: #9a9fbf;
}


