/* ----Friends posts---- */
.newsfeedBox{
    align-items: center;
    height: 100vh;
    width: 100%;
    background-color: #EDF2F6;
}
.newPostBox{
    display: flex;
    background-color: #fff;
    flex-direction: column;
    margin-bottom: 15px;
    border-radius: 5px;
    padding: 0 10px;
}
.newsfeedHeader{
    color: #515365;
    display: block;
    border-left: 3px solid #f5604f;
    padding:  0 10px;
    font-size: 18px;
}
hr {
    width: 95%;
    margin-left: auto;
    margin-right: auto;
    margin-top: -22px;
    margin-bottom: 14px;
    background-color: #9a9fbf;
}
.newPostBox > textarea{
    border: none;
    height: 25px;
    width: 100%;
    color: #757a95;
    font-size: 14px;
    font-family: 'Roboto', serif;
}
.postBtn{
    background-color: #23d2e2;
    color: white;
    font-weight: 600;
    border: 1px solid #23d2e2;
    padding: 8px 12px;
    margin: 10px;
    border-radius: 12px;
    cursor: pointer;
    position: relative;
    width: 30%;
    transition: all 0.2s linear 0s; 
}

.postBtn:hover{
    background-color: #f5604f;
    color: white;
    border: 1px solid #f5604f;
    padding: 8px 12px;
    border-radius: 12px;
}

/* -----Friends post----- */
.postCard {
    background: #fff none repeat scroll 0 0;
    border: 1px solid #ede9e9;
    border-radius: 5px;
    display: inline-block;
    width: 100%;
    margin-bottom: 20px;
    padding: 20px 0;
    position: relative;
}
.cardTop{
    position: absolute;
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    padding: 15px;
}
.friendInfo{
    position: relative;
    width: 100%;
    text-align: left;
    left: 13px;
    color: #757a95;
    font-size: 12px;
    line-height: 0.7em;
}
.avatarImg {
    width: 48px;
    height: 48px;
    object-fit: cover;
    border-radius: 100%;
    position: relative;
    border: 3px solid white;
    top: 0px;
    left: 5px;
}
.friendInfo .published span{
    font-size: 12px;
    color: #757a95;
    position: relative;
    text-align: left;
    top: 0;
}
.friendName a{
    text-decoration: none;
    background-color: transparent;
    text-align: start;
}
.friendName{
    font-size: 15px;
    font-weight: 600;
    color: #f5604f;
    display: inline-block;
    position: relative;
    width: 100%;
    text-align: left;
}
.friendName:hover{
    color: #535165;
}
