.published{
    position: relative;
}

.postMessage {
    font-size: 13px;
    font-weight: 500;
    color: #757a95;
    text-align: left;
    left: 66px;
    position: relative;
    width: 80%;
}
.posts{
    background-color: #EDF2F6;
    width: 70%;

}
.cardBottom{
    position: relative;
    top: 40px;
    left: 15px;
    padding-bottom: 10px;

}
.postImg{
    width: 95%;

}
.likeBtnContainer{
    display: flex;
    flex-direction: row;
    padding: 15px 10px;
    justify-content: center;
    align-items: center;
}
button i{
    border-radius: 100%;
}
.likeBtn {
    width: 30px;
    height: 30px;
    border-radius: 100%;
    background-color: #23d2e2;
    color: white;
    border: none; 
    margin: 0 5px;
}
.dislikeBtn{
    width: 30px;
    height: 30px;
    border-radius: 100%;
    background-color: #f5604f;
    border: none;
    color: white;
    margin: 0 5px;
}
.pinPostBtn{
    width: 30px;
    height: 30px;
    border-radius: 100%;
    background-color: #7750F8;
    border: none;
    color: white;
    margin: 0 5px;
}
.likeBtn:hover{
    transition: all 0.2s;
    border: 1px solid #515365;
    color: #f5604f;
    background-color: white;
}
.dislikeBtn:hover{
    transition: all 0.2s;
    border: 1px solid #515365;
    color: #f5604f;
    background-color: white;
}
.pinPostBtn:hover{
    transition: all 0.2s;
    border: 1px solid #515365;
    color: #f5604f;
    background-color: white;
}



